<template>
    <div class="v-msgbox-wrapper" v-if="value">
        <transition name="msgbox-bounce">
            <div class="shadow-module">
                <p class="hint wrap">{{title}}</p>
                <p class="leave" @click="handleAction('confirm')">{{confirmText}}</p>
                <p class="abolish"  @click="handleAction('cancel')">{{cancelText}}</p>
            </div>
        </transition>
    </div>
</template>

<script>
    import Popup from '../js/utils/popup';
    import vPopup from './v-popup';
    import * as regexp from '../js/regexp/index.js';

    export default {
        mixins: [ Popup ],
        components: {vPopup},
        props: {
            modal: {
                default: true
            },
            confirmText: {  // 
                type: String,
                default: ''
            },
            cancelText: {   // 
                type: String,
                default: ''
            }
        },
        data () {
            return {
                title: '',
                message: '',
                showInput: false,
                inputValue: null,
                showConfirmButton: true,
                showCancelButton: false,
                confirmButtonClass: '',
                cancelButtonClass: '',
                editorErrorMessage: null,
                callback: null
            };
        },

        created () {
        },

        computed: {
            confirmButtonClasses () {
                let classes = 'v-msgbox-btn v-msgbox-confirm ' + this.confirmButtonClass;
                if (this.confirmButtonHighlight) {
                    classes += ' v-msgbox-confirm-highlight';
                }
                return classes;
            },
            cancelButtonClasses () {
                let classes = 'v-msgbox-btn v-msgbox-cancel ' + this.cancelButtonClass;
                if (this.cancelButtonHighlight) {
                    classes += ' v-msgbox-cancel-highlight';
                }
                return classes;
            }
        },

        watch: {
            inputValue (val) {
                if (this.$type === 'prompt') {
                    if (val) {
                        if (val.length) {
                            this.validate();
                        }
                    }
                }
            },

            value (val) {
                this.handleInputType(this.inputType);
                if (val && this.$type === 'prompt') {
                    setTimeout(() => {
                        if (this.$refs.input) {
                            this.$refs.input.focus();
                        }
                    }, 500);
                }
            },

            inputType (val) {
                this.handleInputType(val);
            },

            '$route.name' (val) {
                this.$nextTick(() => {
                    if (!this._inactive) { // 
                    } else {
                    }
                });
            }
        },

        mounted () {
        },

        methods: {
            doClose () {
                this.value = false;
                this._closing = true;

                this.onClose && this.onClose();

                setTimeout(() => {
                    if (this.modal && this.bodyOverflow !== 'hidden') {
                        document.body.style.overflow = this.bodyOverflow;
                        document.body.style.paddingRight = this.bodyPaddingRight;
                    }
                    this.bodyOverflow = null;
                    this.bodyPaddingRight = null;
                }, 200);
                this.opened = false;

                if (!this.transition) {
                    this.doAfterClose();
                }
            },

            handleAction (action) {
                if (this.$type === 'prompt' && action === 'confirm' && !this.validate()) {
                    return;
                }
                let callback = this.callback;
                this.editorErrorMessage = '';
                this.editorErrorMessage = '';
                this.value = false;
                callback(action);
            },

            validate () {
                if (this.$type === 'prompt') {
                    if (this.regexpName) {
                        if (this.inputValue) {
                            if (!regexp[this.regexpName].test(this.inputValue)) {
                                this.editorErrorMessage = this.errorMessage;
                                return false;
                            }
                        } else {
                            this.editorErrorMessage = '';
                            return false;
                        }
                    }
                }
                this.editorErrorMessage = '';
                return true;
            },

            handleInputType (val) {
                if (val === 'range' || !this.$refs.input) return;
                this.$refs.input.type = val;
            }
        }

    };
</script>
<style rel="stylesheet/scss" lang="scss">

    @import "../scss/variables";
    @import "../scss/mixins";

    .v-msgbox-wrapper {
        width: 100%;
        height: 100%;
        @include sizing;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 999;
        .shadow-module {
            width: 100%;
            overflow: hidden;
            background-color: #e1e1e1;
            position: absolute;
            bottom: 0;
            left: 0;
            .hint {
                padding: pxtorem(10px) pxtorem(45px);
                font-size: pxtorem(13px);
                color: #8e8e93;
                line-height: pxtorem(18px);
                border-bottom: 1px solid #e1e1e1;
                text-align: center;
                background: #fff;
            }
            .leave {
                font-size: pxtorem(16px);
                line-height: pxtorem(50px);
                color: #0098FF;
                text-align: center;
                margin-bottom: pxtorem(8px);
                background: #fff;
                font-weight: 300;
            }
            .abolish {
                line-height: pxtorem(50px);
                font-size: pxtorem(16px);
                color: #222;
                text-align: center;
                background: #fff;
                font-weight: 300;
            }
        }
    }
</style>
