<template>
    <transition :name="currentTransition" v-on:after-enter="afterEnter" v-on:enter="enter" v-on:enter-cancelled="enterCancelled">
        <div v-if="currentValue" class="v-popup" :class="[position ? 'v-popup-' + position : '']">
            <slot ref="slotor"></slot>
        </div>
    </transition>
</template>

<script type="text/babel">
    import Popup from '../js/utils/popup';

    /**
     *  v-pickermaskgradient
     *  transitionslidetranslate3dfade
     */
    export default {
        name: 'mt-popup',

        mixins: [Popup],

        props: {
            value: {
                type: Boolean,
                default: false
            },

            modal: {
                default: true
            },

            modalFade: {
                default: true
            },

            lockScroll: {
                default: false
            },

            closeOnClickModal: {
                default: true
            },

            popupTransition: {
                type: String,
                default: '' // popup-fadepopup-slide
            },

            position: {
                type: String,
                default: 'bottom'
            }
        },

        data () {
            return {
                currentValue: false,
                currentTransition: this.popupTransition
            };
        },

        watch: {
            currentValue (val) {
                /**
                 * windowscroll
                 * windowwindow
                 * input
                 * windowfixeddiv
                 */
                let touchObj = document.getElementsByClassName('page')[0];
                if (val && touchObj) {
                    touchObj.style.overflow = 'hidden';
                } else if (touchObj) {
                    touchObj.style.overflow = 'auto';
                }
                this.$emit('input', val);
            },

            value (val) {
                this.currentValue = val;
            }
        },

        beforeMount () {
            // if (this.popupTransition !== 'popup-fade') {
            //     this.currentTransition = `popup-slide-${this.position}`;
            // }
        },

        mounted () {
            if (this.value) {
                this.rendered = true;
                this.currentValue = true;
                this.open();
            }
        },

        methods: {
            touchHandler (e) {
                e = e || window.event;
                if (e && e.preventDefault) {
                    e.preventDefault();
                    e.stopPropagation();
                } else {
                    e.returnvalue = false;
                    return false;
                }
            },

            enter: function (el, done) {
                // if (this.$el.querySelectorAll('.picker-center-mask')[0]) {
                //     let maskor = this.$el.querySelectorAll('.picker-center-mask')[0];
                //     if (maskor && maskor.style) {
                //         maskor.style.top = 0;
                //     }
                // }
                console.log('v-popup.enter: ');
            },
            afterEnter (el) {
                console.log('v-popup.afterEnter: ');
            },
            enterCancelled: function (el) {
//                this.eventbus;
                console.log('v-popup.enterCancelled: ');
            }
        }
    };
</script>

<style lang="scss" rel="stylesheet/scss">
    @import "../scss/variables";
    @import "../scss/mixins";

    .v-modal-enter {
        animation: v-modal-in 0.2s ease;
    }

    .v-modal-leave {
        animation: v-modal-out 0.2s ease forwards;
    }

    @keyframes v-modal-in {
        0% {
            opacity: 0;
        }
    }
    @keyframes v-modal-out {
        to {
            opacity: 0;
        }
    }

    .v-modal {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        opacity: 0.5;
        background: #000;
        transition: 0.5s;
    }

    .v-popup {
        width: 100%;
        position: fixed;
        transition: opacity 0.1s, transform 0.5s;
        z-index: 300;
        background: #fff;
    }

    .v-popup-top {
        top: 0;
        right: auto;
        bottom: auto;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
    }

    .v-popup-right {
        top: 50%;
        right: 0;
        bottom: auto;
        left: auto;
        transform: translate3d(0, -50%, 0);
    }

    .v-popup-bottom {
        bottom: 0;
        left: 0;
        opacity: 0.99;
    }

    .v-popup-left {
        top: 50%;
        right: auto;
        bottom: auto;
        left: 0;
        transform: translate3d(0, -50%, 0);
    }

    .popup-slide-top-enter, .popup-slide-top-leave-active {
        transform: translate3d(-50%, -100%, 0);
    }

    .popup-slide-right-enter, .popup-slide-right-leave-active {
        transform: translate3d(100%, -50%, 0);
    }

    .popup-slide-bottom-enter, .popup-slide-bottom-leave-active {
        transform: translate3d(-50%, 100%, 0);
    }

    .popup-slide-left-enter, .popup-slide-left-leave-active {
        transform: translate3d(-100%, -50%, 0);
    }

    .popup-fade-enter, .popup-fade-leave-active {
        opacity: 0;
    }

    .popup-fade-enter-to,
    .popup-slide-bottom-enter-to {
        visibility: visible;
    }

    .popup-fade-enter-to {
        transform: translateY(0);
        opacity: 1;
    }
</style>
