<template>
    <v-popup v-model="visible" position="bottom" class="v-datetime">
        <span class="v-datetime-action v-datetime-cancel" @click="visible = false">{{$t('btn.cancel')}}</span>
        <span class="v-datetime-action v-datetime-confirm" @click="confirm">{{$t('btn.confirm')}}</span>
        <div class="picker" :class="{ 'picker-3d': rotateEffect }">
            <div class="picker-toolbar" v-if="slots.default">
                <slot></slot>
            </div>
            <div class="picker-items">
                <picker-slot v-for="(slot, idx) in slots"
                             :key="idx"
                             :valueKey="valueKey"
                             :values="slot.values || []"
                             :text-align="slot.textAlign || 'center'"
                             :visible-item-count="visibleItemCount"
                             :class-name="slot.className"
                             :flex="slot.flex"
                             v-model="values[slot.valueIndex]"
                             :rotate-effect="rotateEffect"
                             :divider="slot.divider"
                             :content="slot.content"
                             :timeShow="slot.show"
                ></picker-slot>
                <div ref="mask" class="picker-center-mask"></div>
                <div class="picker-center-highlight" v-if="isHide">{{$t('publish.day')}}</div>
            </div>
        </div>
    </v-popup>
</template>

<script>
    import vPopup from './v-popup';
    import pickerSlot from './v-picker-slot';
    import { constants } from 'crypto';

    export default {
        name: 'v-picker',
        componentName: 'picker',

        components: { vPopup, pickerSlot },

        props: {
            slots: {
                type: Array
            },
            visibleItemCount: {
                type: Number,
                default: 5
            },
            valueKey: String,
            rotateEffect: {
                type: Boolean,
                default: false
            },
            isHide: {
                type: Boolean,
                default: true
            },
            defaultVal: {
                type: String,
                default: ''
            }
        },

        data () {
            return {
                visible: false,
                selectId: ''
            };
        },

        computed: {
            values () {
                var slots = this.slots || [],
                    values = [];
                slots.forEach(function (slot) {
                    if (!slot.divider) values.push(slot.value);
                });

                return values;
            },
            slotCount () {
                var slots = this.slots || [],
                    result = 0;
                slots.forEach(function (slot) {
                    if (!slot.divider) result++;
                });
                return result;
            }
        },

        created () {
            this.$on('slotValueChange', this.slotValueChange);
            var slots = this.slots || [], valueIndexCount = 0;
            let values = [];
            slots.forEach(function (slot) {
                if (!slot.divider) {
                    slot.valueIndex = valueIndexCount++;
                    values[slot.valueIndex] = (slot.values || [])[slot.defaultIndex || 0];
                }
            });
        },

        mounted () {
        },

        methods: {
            open (tag) {
                if (this.defaultVal === '') {
                    this.values[0] = 15;
                } else {
                    this.values[0] = this.defaultVal;
                }
                this.visible = true;
                this.selectId = tag;
            },

            close () {
                this.visible = false;
            },
            slotValueChange () {
                this.$emit('change', this, this.values);
            },
            getSlot (slotIndex) {
                var slots = this.slots || [], count = 0, target,
                    children = this.$children.filter(child => child.$options.name === 'picker-slot');
                slots.forEach(function (slot, index) {
                    if (!slot.divider) {
                        if (slotIndex === count) {
                            target = children[index];
                        }
                        count++;
                    }
                });

                return target;
            },
            getSlotValue (index) {
                var slot = this.getSlot(index);
                if (slot) {
                    return slot.value;
                }
                return null;
            },
            setSlotValue (index, value) {
                var slot = this.getSlot(index);
                if (slot) {
                    slot.currentValue = value;
                }
            },
            getSlotValues (index) {
                var slot = this.getSlot(index);
                if (slot) {
                    return slot.mutatingValues;
                }
                return null;
            },
            setSlotValues (index, values) {
                var slot = this.getSlot(index);
                if (slot) {
                    slot.mutatingValues = values;
                }
            },
            getValues () {
                return this.values;
            },
            setValues (values) {
                var slotCount = this.slotCount;
                values = values || [];
                if (slotCount !== values.length) {
                    throw new Error('values length is not equal slot count.');
                }
                values.forEach((value, index) => {
                    this.setSlotValue(index, value);
                });
            },
            confirm () {
                this.visible = false;
                this.$emit('confirm', this.values[0], this.selectId);
            }
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "../scss/variables";
    @import "../scss/mixins";

    $line-height: pxtorem(36px);

    .picker {
        overflow: hidden;
    }

    .picker-toolbar {
        height: pxtorem(40px);
    }

    .picker-items {
        text-align: right;
        font-size: pxtorem(24px);
        position: relative;
        padding: 0 pxtorem(15px);
        @include box_flex;
        @include justify-content(center);
        @include align_items(center);
    }

    .picker-center-mask {
        width: 100%;
        height: 100.1%;
        position: absolute;
        left: 0;
        top: -1px;
        background: linear-gradient(180deg, hsla(0, 0%, 100%, 0.95), hsla(0, 0%, 100%, 0.6)), linear-gradient(0deg, hsla(0, 0%, 100%, 0.95), hsla(0, 0%, 100%, 0.6));
        background-position: top, bottom;
        background-size: 100% 42%;
        background-repeat: no-repeat;
        pointer-events: none;
        z-index: 9;
    }

    .picker-center-highlight {
        height: $line-height;
        box-sizing: border-box;
        position: absolute;
        left: 0;
        width: 100%;
        top: 50%;
        margin-top: pxtorem(-18px);
        pointer-events: none;
        padding-left: 58%;
        text-align: left;
        line-height: pxtorem(36px);
        font-size: pxtorem(16px);
    }

    .picker-center-highlight::before,
    .picker-center-highlight::after {
        content: "";
        position: absolute;
        height: pxtorem(1px);
        width: 100%;
        background-color: #dddee3;
        display: block;
        z-index: 15;
        transform: scaleY(0.5);
    }

    .picker-center-highlight::before {
        left: 0;
        top: 0;
        bottom: auto;
        right: auto;
    }

    .picker-center-highlight::after {
        left: 0;
        bottom: 0;
        right: auto;
        top: auto;
    }

    .v-datetime {
        width: 100%;
    }

    .v-datetime-action {
        display: inline-block;
        width: 50%;
        text-align: center;
        line-height: pxtorem(40px);
        font-size: pxtorem(16px);
        color: #007aff;
    }

    .v-datetime-cancel {
        float: left;
        text-align: left;
        padding-left: pxtorem(15px);
    }

    .v-datetime-confirm {
        text-align: right;
        padding-right: pxtorem(15px);
    }
</style>
